.navbar {
  display: flex;
  justify-content: space-around;
  padding: 10px;
  box-shadow: 0px 1px 3px -2px black;
}
.nav-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  place-items: center;
  gap: 10px;
}
.nav-logo p {
  color: #171717;
  font-size: 28px;
  font-weight: 600;
  align-items: center;
  place-items: center;
  display: flex;
  justify-content: center;
  text-align: center;
}
.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  gap: 50px;
  color: #626262;
  font-size: 20px;
  font-weight: 500;
}
.nav-menu li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3px;
  cursor: pointer;
  font-size: 1rem;
}
.nav-menu hr {
  border: none;
  width: 80%;
  height: 3px;
  border-radius: 10px;
  background: #ff4141;
}
.nav-login-cart {
  display: flex;
  align-items: center;
  gap: 45px;
}
.nav-login-cart button {
  width: 157px;
  height: 58px;
  outline: none;
  border: 1px solid #7a7a7a;
  border-radius: 75px;
  color: #515151;
  font-size: 20px;
  font-weight: 500;
  background: white;
  cursor: pointer;
}
.nav-login-cart button:active {
  background: #f3f3f3;
}
.nav-cart-count {
  width: 22px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -30px;
  margin-left: -55px;
  border-radius: 11px;
  font-size: 14px;
  background: #ff4141;
  color: #f3f3f3;
}
